::placeholder {
  color: #ddd;
}
input,
button {
  display: inline-block;
  width: 100%;
  height: 3.23rem;
  border: none;
  outline: none;
  border-radius: 100px;
  background: rgba(255, 255, 255, 0.15);
}
textarea {
  display: inline-block;
  width: 100%;
  height: 3.23rem;
  border: none;
  outline: none;
  border-radius: 50px;
  background: rgba(255, 255, 255, 0.15);
  resize: none;
}
.login {
  min-height: 100vh;
  width: 100%;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login .container {
  position: relative;
  width: 90%;
  max-width: 420px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #edeff1;
  background-size: cover;
  border-radius: 2rem;
}
.login .title {
  font-size: 1.8rem;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
}
.login .social-login {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.login .social-login button {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 10rem;
  margin: 0.75rem;
  padding: 0.625rem;
  text-transform: uppercase;
  letter-spacing: 0.7px;
  cursor: pointer;
  transition: background 0.3s ease-in;
}
.login .social-login button:hover {
  background: rgba(255, 255, 255, 0.3);
}
.login .social-login button i {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.25rem;
  height: 2.25rem;
  background-color: #fff;
  border-radius: 50%;
  font-size: 1.4rem;
  padding-left: 2px;
  text-align: center;
  margin-right: 0.25rem;
}
.login .social-login button i.fa-twitter {
  color: #00acee;
}
.login .social-login button i.fa-google {
  color: #ff3e30;
}
.login .separator {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login .separator span {
  display: block;
  width: 100%;
  height: 1px;
  background: #ddd;
  margin: 1.5rem;
}
form {
  width: 100%;
}
form .form-group {
  margin-bottom: 1.5rem;
}
form .form-group label {
  display: block;
  letter-spacing: 0.5px;
  margin-bottom: 0.5rem;
  padding-left: 1.5rem;
  color: dark-blue;
}
form .form-group input {
  letter-spacing: 0.5px;
  font-size: 1.1rem;
  padding: 0 1.5rem;
  border: 1px solid gray;
}
form .form-group textarea {
  letter-spacing: 0.5px;
  font-size: 1.1rem;
  padding: 0.9rem 1.5rem;
  border: 1px solid gray;
}
button[type="submit"] {
  background: #ecbb08;
  color: #023262;
  font-size: 1.3rem;
  font-weight: bold;
  letter-spacing: 1px;
  cursor: pointer;
  margin: 1rem 0;
  transition: all 0.3s linear;
}
button[type="submit"]:hover {
  background: #ffc800;
  opacity: 0.8;
}
.additional-act {
  width: 100%;
  text-align: center;
  letter-spacing: 0.7px;
  margin: 0.5rem 0;
}
.additional-act span {
  color: #ecbb08;
  cursor: pointer;
}
